/* Categories */

.genreList {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
}

.genreListTitle {
    font-family: var(--font-family-titles);
    text-align: center;
    margin-bottom: 30px;
}

.category-container {
    margin: 0 auto;
    width: 45%;
}

.category-title {
    margin-bottom: 8px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: var(--font-family-text);
    width: 100%;
}

.category-title::before {
    content: '••••';
    color: transparent;
    /* TODO: cambiar color por ID de la categoría */
    background-color: var(--idColor);
    border-radius: 8px;
    margin-right: 8px;
    width: 8px;
    height: 8px;
}

